import React, { useState, useEffect, useRef } from "react";
import styled from "styled-components";
import { Link } from "gatsby";
import { colors, device } from "@layouts/styleConfig";
import { useTheme } from "styled-components";

const Layout = styled.div`
  padding: 4px 12px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  box-shadow: 0px 0px 2px ${colors.grey};

  label.hamburger_menu {
    width: 30px;
    height: 30px;
    box-sizing: border-box;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
  }

  .laptop_menu {
    display: none;
  }

  .title {
    line-height: 16px;
    font-size: 16px;
    font-weight: 900;
    white-space: pre;
    color: ${({ theme }) => theme.color};
    text-decoration: none;
    :hover {
      cursor: pointer;
    }
  }

  input#hamburger_menu {
    display: none;
  }

  .mobile_menu_hamburger {
    display: block;
    background-color: ${({ theme }) => theme.hoverColor};
    position: relative;
    height: 4px;
    width: 30px;
    transition: all 0.4s ease-in-out;
    -webkit-transition: all 0.4s ease-in-out;

    border-radius: 2px;
    margin: 3px 0;
  }

  .line1 {
    transform-origin: 0% 0%;
    transition: all 0.4s ease-in-out;
    -webkit-transition: all 0.4s ease-in-out;
  }
  .line2 {
    transition: all 0.2s ease-in-out;
    -webkit-transition: all 0.4s ease-in-out;
  }
  .line3 {
    transform-origin: 0% 100%;
    transition: all 0.4s ease-in-out;
    -webkit-transition: all 0.4s ease-in-out;
  }
  input#hamburger_menu:checked ~ * .line1 {
    transform: rotate(45deg);
  }
  input#hamburger_menu:checked ~ * .line2 {
    transform: scale(0);
  }
  input#hamburger_menu:checked ~ * .line3 {
    transform: rotate(-45deg);
  }

  .mobile_menu_content {
    opacity: 0;
    background: ${({ theme }) => theme.qColor};
    position: absolute;
    top: 40px;
    left: 0;
    height: calc(100vh - 40px);
    width: 100%;
    transition: 0.3s;
    z-index: -1;

    display: flex;
    flex-direction: column;
    padding: 24px;

    > a {
      margin: 24px 0;
      color: ${({ theme }) => theme.color};
      font-size: 24px;
      text-decoration: none;
      border-bottom: 1px solid ${({ theme }) => theme.contentColor};
      text-align: center;

      :hover {
        margin-bottom: 23px;
        border-bottom: 2px solid ${({ theme }) => theme.color};
      }
    }
  }

  input#hamburger_menu:checked ~ .mobile_menu_content {
    opacity: 0.9;
    z-index: 2;
  }

  ul {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
  }

  @media ${device.tablet} {
    height: 70px;
    padding: 0px 12px;
    label.hamburger_menu,
    .mobile_menu_content {
      display: none;
    }

    .title,
    .contact {
      line-height: 20px;
      font-size: 20px;
      :hover {
        cursor: pointer;
      }
    }

    .laptop_menu {
      display: block;

      display: flex;
      list-style: none;

      font-size: 20px;
      font-weight: 900;
      white-space: pre;
      color: ${({ theme }) => theme.color};

      > a {
        margin: 2px 16px;
        text-decoration: none;
        color: ${({ theme }) => theme.postColor};
        line-height: 30px;

        :hover {
          cursor: pointer;
          color: ${({ theme }) => theme.hoverColor};
          margin-bottom: 0px;
          border-bottom: 2px solid ${({ theme }) => theme.hoverColor};
          transition: 0.3s;
        }

        &.activePath {
          color: ${({ theme }) => theme.hoverColor};
          border-bottom: 2px solid ${({ theme }) => theme.hoverColor};
          margin-bottom: 0px;
        }
      }
    }
  }
`;

const SwitchThemeLayout = styled.div.withConfig({
  componentId: "SwitchThemeLayout",
})`
  position: absolute;
  right: 100px;

  display: flex;
  align-items: center;

  input {
    display: none;
  }

  label {
    position: absolute;
    width: 40px;
    height: 24px;
    border-radius: 15px;
    box-sizing: border-box;
    z-index: 2;
    overflow: hidden;
    :hover {
      cursor: pointer;
    }

    span {
      position: absolute;
      top: 2px;
      left: ${({ isDarkTheme }) => (isDarkTheme ? "18px" : "2px")};
      width: 20px;
      height: 20px;
      border-radius: 50%;
      box-sizing: border-box;
      transition: all ease-in-out 0.3s;
      color: ${({ isDarkTheme }) => (isDarkTheme ? "#fff" : "#ff0")};
      background-color: ${({ isDarkTheme }) => (isDarkTheme ? "#fff" : "#ff0")};
      border: 2px solid
        ${({ isDarkTheme }) => (isDarkTheme ? "#e8e8ea" : "#f5e216")};

      &:before {
        content: ".";
        position: absolute;

        ${({ isDarkTheme }) =>
          isDarkTheme
            ? ` top: -8px;
                left: 0px;
                color: #e8e8eaaa;
                transform: scale(1.5);`
            : ` top: -10px;
                left: 20px;
                color: #fff;
                transform: scale(3.5);`}
      }
      &:after {
        content: ".";
        position: absolute;

        ${({ isDarkTheme }) =>
          isDarkTheme
            ? ` top: -16px;
                left: 3px;
                color: #e8e8ea;
                transform: scale(2.2);`
            : ` top: -14px;
                left: 24px;
                color: #fff;
                transform: scale(4);`}
      }
    }
  }

  .background {
    background: ${({ isDarkTheme }) => (isDarkTheme ? "#343434" : "skyblue")};
    width: 40px;
    position: absolute;
    height: 24px;
    z-index: 1;
    border-radius: 15px;

    &:before {
      content: ".";
      position: absolute;
      top: -3px;
      left: 4px;
      color: #ffeeaafc;
    }
    &:after {
      content: ".";
      position: absolute;
      top: 4px;
      left: 10px;
      color: #ffeeaafc;
    }
  }

  @media ${device.tablet} {
    right: 150px;
  }
`;

export default function Header({
  toggleModal = () => {},
  toggleTheme = () => {},
  path,
}) {
  const [menuOpen, setMenuOpen] = useState(() => false);
  const toggleMenu = () => setMenuOpen((t) => !t);

  const hamburgetMenuCheckRef = useRef(null);
  const theme = useTheme();
  const currentTheme = theme?.type || "dark";
  const isDarkTheme = currentTheme === "dark";

  useEffect(() => {
    const html = document.querySelector("body");
    if (hamburgetMenuCheckRef.current) {
      menuOpen
        ? (html.style.overflow = "hidden")
        : (html.style.overflow = "visible");
    }
  }, [menuOpen]);

  useEffect(() => {
    setMenuOpen(false);
  }, [path]);

  return (
    <Layout>
      <Link to="/" className="title">{`Denny \nChang`}</Link>
      <ul className="laptop_menu">
        <Link to="/blog" activeClassName={"activePath"}>
          Blog
        </Link>
        <Link to="/about" activeClassName={"activePath"}>
          About
        </Link>
        <Link to="/project" activeClassName={"activePath"}>
          Project
        </Link>
      </ul>
      <SwitchTheme isDarkTheme={isDarkTheme} toggleTheme={toggleTheme} />
      <div
        className="contact laptop_menu"
        onClick={toggleModal}
        role="button"
        tabIndex="0"
        onKeyPress={(e) => {
          if (e.key === "Enter") toggleModal();
        }}
      >
        Contact
      </div>

      <input
        type="checkbox"
        id="hamburger_menu"
        checked={menuOpen}
        onChange={toggleMenu}
        ref={hamburgetMenuCheckRef}
      />

      <label className="hamburger_menu" htmlFor="hamburger_menu">
        <span className="mobile_menu_hamburger line1" />
        <span className="mobile_menu_hamburger line2" />
        <span className="mobile_menu_hamburger line3" />
      </label>
      <ul className="mobile_menu_content">
        <Link to="/blog" activeClassName={"activePath"}>
          Blog
        </Link>
        <Link to="/about" activeClassName={"activePath"}>
          About
        </Link>
        <Link to="/project" activeClassName={"activePath"}>
          Project
        </Link>
      </ul>
    </Layout>
  );
}

function SwitchTheme({ isDarkTheme, toggleTheme }) {
  return (
    <SwitchThemeLayout isDarkTheme={isDarkTheme}>
      <input
        type="checkbox"
        id="switchTheme"
        value={isDarkTheme}
        onChange={toggleTheme}
      />
      <label className="switchTheme" htmlFor="switchTheme">
        <span className="toggle" />
      </label>
      <div className="background" />
    </SwitchThemeLayout>
  );
}
