import React, { useState } from "react";
// import { Link } from "gatsby";
import styled from "styled-components";
import { colors, device } from "@layouts/styleConfig";
import Loading from "../Loading";

const Layout = styled.form.withConfig({ componentId: "ContactForm_Layout" })`
  display: flex;
  flex-direction: column;
  justify-content: center;

  p {
    text-align: center;
    margin: 0;
    color: ${colors.black};
  }

  hr {
    width: 80%;
    border: 0.5px solid ${colors.grey};
  }

  .btn_area {
    text-align: end;
    margin-top: 8px;
  }

  button {
    color: ${colors.white};
    background-color: ${colors.blue};
    border: none;
    padding: 8px;
    width: 100%;
    border-radius: 8px;
    transition: 0.3s;

    :hover {
      cursor: pointer;
      filter: brightness(1.1);
    }
  }

  @media ${device.tablet} {
    button {
      padding: 4px 8px;
      width: max-content;
    }
  }
`;

const InputLayout = styled.div`
  display: flex;
  flex-direction: column;
  justify-content: flex-start;

  margin: 8px 0;

  label {
    color: ${colors.darkGrey};
    font-size: 14px;
    margin-bottom: 4px;
  }

  input {
    font-size: 16px;
    line-height: 150%;
    border: 1px solid
      ${({ hasError }) => (hasError ? colors.red : colors.darkGrey)};
    border-radius: 4px;
    padding: 4px;
  }

  textarea {
    resize: none;
    width: 100%;
    height: 100px;
    box-sizing: border-box;
    font-size: 16px;
    line-height: 150%;
    border: 1px solid
      ${({ hasError }) => (hasError ? colors.red : colors.darkGrey)};
    border-radius: 4px;
    padding: 4px;
  }

  @media ${device.tablet} {
  }
`;

const DisplayResultLayout = styled.div.withConfig({
  componentId: "DisplayResult_Layout",
})`
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  min-height: 300px;
  color: ${colors.black};

  p {
    white-space: pre;
    text-align: center;
  }

  button {
    background-color: ${colors.blue};
    color: ${colors.white};
    border: none;
    padding: 4px 12px;
    border-radius: 4px;
    margin-top: 16px;
    :hover {
      cursor: pointer;
      filter: brightness(1.1);
    }
  }
`;

export default function ContactForm({
  onCancel = () => {},
  initialStatus = "initial",
}) {
  const [errors, setErrors] = useState({});
  const [sendingStatus, setSendingStatus] = useState(initialStatus);

  const submitForm = (e) => {
    e.preventDefault();

    const nameValue = e.target[0].value;

    const commentsValue = e.target[1].value;

    if (!nameValue || !commentsValue) {
      setErrors({ name: !nameValue, comments: !commentsValue });
      return;
    }

    setSendingStatus("loading");

    const requestBody = JSON.stringify({
      user: nameValue,
      message: commentsValue,
    });

    fetch(`${process.env.GATSBY_MESSAGE_API}`, {
      method: "POST",
      body: requestBody,
    })
      .then(() => {
        setSendingStatus("success");
      })
      .catch(() => {
        setSendingStatus("error");
      });
  };

  const renderLayout = {
    initial: (
      <Layout id="contact_form" onSubmit={submitForm}>
        <p>歡迎留下任何你想說的話 🙏</p>
        <hr />
        <Input
          id="name"
          label="你的稱呼"
          hasError={errors["name"]}
          handleError={setErrors}
        />

        <Textarea
          id="comments"
          label="想說的話"
          hasError={errors["comments"]}
          handleError={setErrors}
        />

        <div className="btn_area">
          <button htmlFor="contact_form">送出</button>
        </div>
      </Layout>
    ),
    loading: (
      <DisplayResultLayout>
        <Loading />
      </DisplayResultLayout>
    ),
    success: (
      <DisplayResultLayout>
        <p>🎉 已成功送出 🎉</p>
        <button onClick={onCancel}>關閉</button>
      </DisplayResultLayout>
    ),
    error: (
      <DisplayResultLayout>
        <p>抱歉😿{"\n"}好像有什麼出錯了</p>
      </DisplayResultLayout>
    ),
  };

  return renderLayout[sendingStatus];
}

function Input({ id, label, hasError, handleError = () => {} }) {
  return (
    <InputLayout hasError={hasError}>
      <label htmlFor={id}>{label}</label>
      <input
        type="text"
        id={id}
        onFocus={() => handleError((prev) => ({ ...prev, [id]: false }))}
      />
    </InputLayout>
  );
}

function Textarea({ id, label, hasError, handleError = () => {} }) {
  return (
    <InputLayout hasError={hasError}>
      <label htmlFor={id}>{label}</label>
      <textarea
        type="text"
        id={id}
        onFocus={() => handleError((prev) => ({ ...prev, [id]: false }))}
      />
    </InputLayout>
  );
}
